.cardWrapper{
   box-shadow: 0px 2px 4px 0px rgba(0,0,0,1);
   -webkit-box-shadow: 0px 2px 4px 0px rgba(0,0,0,1);
   -moz-box-shadow: 0px 2px 4px 0px rgba(0,0,0,1);
}
.profile{
   width: 50px;
   height: 50px;
   border-radius: 50%;
}
.leftTopCard{
   flex: 1;
   display: flex;
   align-items: center;
   padding: 10px;
   border-bottom: 1px solid #EAEBEB;
}
.rightTopCard{
   padding: 10px;
}
.rightTopCardImg{
   width: 70px;
   height: 70px;
}
.mainCardWrapper{
   padding: 7px 15px;
}
.userName{
   font-size: 17px;
   line-height: 25px;
   font-weight: 500;
}
.userEmail{
   font-size: 14px;
   font-weight: 200;
}
.middleCard{
   padding: 20px;
   display: flex;
}
.middleRight{
   flex: 1;
}
.middleLeft{
   flex: 1;
}
.middleLabel{
   font-size: 14px;
   line-height: 22px;
   font-weight: 500;
}
.middleText{
   font-size: 16px;
   line-height: 22px;
   font-weight: 300;
}
.middleBorder{
   border: 1px solid #EAEBEB;
   margin: 0 30px;
}
.bottomCard{
   background-color: #F9F9F7;
   display: flex;
}
.leftBottom{
   flex: 2;
   padding: 8px;
   font-size: 16px;
   display: flex;
   align-items: center;
   justify-content: space-around;
}
.rightBottom{
   flex: 1;
}
.btnCancel{
   background-color: #E20000;
   height: 45px;
   line-height: 45px;
   text-align: center;
}
.leftBottomLabel{
   border-right: 1px solid #e2e2e2;
   padding-right: 20px;
}